<template>
  <div>
    <section>
      <div class="breadcrumb">
        <ul class="breadcrumb_list container">
          <li><router-link to="/" class="breadcrumb_link">TRANG CH</router-link></li>
          <li><span class="breadcrumb_link">></span></li>
          <li><span class="breadcrumb_link">CHI TIT SN PHM</span></li>
        </ul>
      </div>
    </section>

    <section>
      <div id="prodetails" class="section_p1">
        <div class="single_pro_image">
          <img :src="currentImage" width="100%" id="MainImg" alt="" />
          <div class="small_img_group">
            <div
              class="small_img_col"
              v-for="(img, index) in smallImages"
              :key="index"
            >
              <img
                :src="img"
                width="100%"
                class="small_img"
                alt=""
                v-on:click="handleImage(img)"
              />
            </div>
          </div>
        </div>
        <div class="single_pro_details">
          <h6>SN PHM</h6>
          <h5>{{ product.name }}</h5>
          <div class="gia">
            <p>GI:</p>
            <h2>{{ formattedPrice(product.price) }}</h2>
          </div>
          <div class="select_1">
            <select>
              <option>Select Size</option>
              <option>23-25mm</option>
              <option>26-29mm</option>
              <option>34-36mm</option>
              <option>42-45mm</option>
            </select>
            <input v-model="quantity" type="number" value="1" min="1" />
            <router-link v-if="product.inStock" to="/rohang">
              <button v-on:click="handleBuyItem">THM VO R</button>
            </router-link>
            <button v-else id="btndisabled">Ht Hng</button>
          </div>
          <p v-if="product.inStock" style="color: green; font-weight: 600">
            Sn phm cn hng!
          </p>
          <p v-else style="color: red; font-weight: 600">Sn phm ht hng!</p>
          <span>{{ product.description }}</span>
        </div>
      </div>
      <div class="product_details_content">
        <div class="product_details_btn">
          <button class="mo_ta active_details">M T</button>
          <button class="mo_ta">NH GI</button>
        </div>
        <div class="details_content active_details">
          <div class="details_title">
            <h5>M T SN PHM</h5>
          </div>
          <div class="content_detail">
            <div>
              <p>
                L s kt hp hi ha gia kiu dng v cht lng, nhng chic
                ng h nam CURREN xng ng l mt la chn ca phi mnh.
                Chic ng h nam CURREN c thit k  dnh ring cho nhng
                chng trai tr trung, nng ng. S thit k mt trn nam tnh
                v tinh t, cc chi tit trang tr nh vch chia gi, kim ng
                h... c b tr mt cch hi ha, t tnh thm m cao, sn
                phm gp phn to nn phong cch ring cho bn. Hn th cng vi
                tnh ng dng cao, bn c th kt hp chic ng h nam CURREN
                ny vi nhng b trang phc khc nhau  to nn phong cch
                ring y cun ht.
              </p>
            </div>
            <div>
              <p>
                THNG S K THUT<br />
                 Thng hiu: CURREN<br />
                 Kiu my: Quartz<br />
                 Dnh cho: nam<br />
                 Cht liu v: Thp khng g<br />
                 Cht liu mt trc: Knh cng<br />
                 Cht liu dy: Da ln<br />
                 Kch thc mt: 54mm<br />
                  dy mt: 14mm<br />
                 Chiu di dy: 240mm<br />
                  rng dy: 24mm<br />
                 S kim: 3, c lch<br />
                  chu nc: 3ATM<br />
                 Nng lng s dng: Pin<br />
                 Bo hnh: Bo hnh ng h 12 thng
              </p>
            </div>
            <div>
              <p>
                 GI  S DNG V BO DNG<br />
                 Nn trnh:<br />
                - Va chm v tip xc vi cht n mn, nhit  cao hoc t
                trng mnh.<br />
                - S dng bt k dung mi, cht lm sch, cht ty cng nghip,
                cht dnh, sn hoc cc cht xt m phm.<br />
                - eo ng h cng cc vng eo tay v d to vt xc hay lm
                hng mt ng h.<br />
                - Chnh nt chnh gi khi ng h b t.<br />
                -  ng h ni nhit  thay i t ngt.<br />
                - S dng, nhn cc nt hoc ci t  di nc.<br />

                Nn lm:<br />
                - Lm sch ng h theo nh k vi mm hi m  gia tng tui
                th ng h.<br />
                - Nu xut hin hi nc ngng t, cn mang ng h n Trung
                Tm Dch V Khch Hng ca cc hng cng nhanh cng tt  gim
                thiu h hng bn trong.<br />
                - Thay pin khi ng h chy sai gi hoc chuyn ng kim giy
                khng u v r r in do pin cht c th gy ra hng hc
                nghim trng cho ng h.
              </p>
            </div>
          </div>
        </div>
        <div class="details_content">
          <h3>nh gi</h3>
          <p>Cha c nh gi no</p>
          <div class="form_judge">
            <h5>nh gi ca bn</h5>
            <div class="star_judge">
              <div class="star_star active_star">
                <i class="ri-star-fill"></i>
              </div>
              <span>|</span>
              <div class="star_star">
                <i class="ri-star-fill"></i>
                <i class="ri-star-fill"></i>
              </div>
              <span>|</span>
              <div class="star_star">
                <i class="ri-star-fill"></i>
                <i class="ri-star-fill"></i>
                <i class="ri-star-fill"></i>
              </div>
              <span>|</span>
              <div class="star_star">
                <i class="ri-star-fill"></i>
                <i class="ri-star-fill"></i>
                <i class="ri-star-fill"></i>
                <i class="ri-star-fill"></i>
              </div>
              <span>|</span>
              <div class="star_star">
                <i class="ri-star-fill"></i>
                <i class="ri-star-fill"></i>
                <i class="ri-star-fill"></i>
                <i class="ri-star-fill"></i>
                <i class="ri-star-fill"></i>
              </div>
            </div>
            <p>Nhn xt ca bn</p>
            <label for="avatar">Chn hnh nh:</label>
            <input type="file" id="avatar" accept="image/png,image/jpg" />
            <textarea
              name="comment"
              id="comment"
              cols="130"
              rows="10"
            ></textarea>
            <button>GI I</button>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Navigation from "../components/Navigation.vue";
import CompHeader from "../components/CompHeader.vue";
import CompFooter from "../components/CompFooter.vue";
import ListItemNew from "../components/ListItemNew.vue";
import ListTrackMark from "../components/ListTrackMark.vue";
import ListItemProducts from "../components/ListItemProducts.vue";
import CompBlog from "../components/CompBlog.vue";
import { mapGetters } from "vuex";
import { mapActions } from "vuex";

export default {
  name: "App",
  data() {
    return {
      smallImages: [
        "/dist/img/p1.png",
        "/dist/img/p2.png",
        "/dist/img/p3.png",
        "/dist/img/p4.png",
      ],
      currentImage: "",
      quantity: 1,
      //
    };
  },
  components: {
    Navigation,
    CompHeader,
    CompFooter,
    ListItemNew,
    ListTrackMark,
    ListItemProducts,
    CompBlog,
  },
  computed: {
    ...mapGetters(["getProductById"]),
    product() {
      return this.getProductById(this.$route.params.id);
    },
    defaultImage() {
      // S dng getter  ly gi tr mc nh ca hnh nh t store hoc t cc trng thi khc trong ng dng ca bn
      return "/dist/img/" + this.product.image;
    },
  },
  created() {
    // Thit lp gi tr mc nh cho currentImage khi component c to
    this.currentImage = this.defaultImage;
  },
  methods: {
    ...mapActions({
      actBuyItem: "cart/actBuyItem",
    }),
    formattedPrice(price) {
      if (price !== undefined) {
        let parts = price.toString().split(".");
        parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        return parts.join(".") + " VND";
      } else {
        return "";
      }
    },
    handleImage(img) {
      this.currentImage = img;
    },
    handleBuyItem(e) {
      let data = {
        actItem: this.product,
        quantity: parseInt(this.quantity),
      };
      this.actBuyItem(data);
      // this.$store.dispatch('cart/actBuyItem',data);
    },
  },
};
</script>

<style>
#btndisabled {
  cursor: not-allowed;
}
</style>



